/* Global unordered list CSS */
.unordered-list ul {
  list-style: none;
  padding-left: 20px;
}

.unordered-list ul li::before {
  content: "\2022";
  color: #25429c;
  font-weight: bold;
  display: inline-block;
  width: 1em;
  margin-left: -1em;
}
.unordered-list ul li .list-imp {
  font-weight: 600;
  position: relative;
}
.unordered-list ul li .list-imp::before {
  position: absolute;
  content: "";
  background-image: url(/static/media/underline.b8edc2a1.svg);
  background-repeat: no-repeat;
  background-position: 0px 0px;
  width: 130px;
  height: 15px;
  top: 25px;
  left: 0%;
}
/* Global unordered list CSS */

/* Global ordered list CSS */
.ordered-list ol {
  list-style: none;
  counter-reset: item;
  margin-bottom: 0px;
}
.ordered-list li {
  counter-increment: item;
  margin-bottom: 20px;
}
.ordered-list li:before {
  margin-right: 10px;
  content: counter(item);
  background: #f8d874;
  border-radius: 100%;
  color: #25429c;
  width: 1.7em !important;
  text-align: center;
  display: inline-block;
  margin-left: -35px !important;
  font-weight: 700;
}
