/* tooltip css */
[data-md-tooltip] {
  position: relative;
  cursor: pointer;
}

[data-md-tooltip]:before {
  content: attr(data-md-tooltip);
  position: absolute;
  bottom: -35px;
  left: 50%;
  padding: 8px;
  -webkit-transform: translateX(-50%) scale(0);
          transform: translateX(-50%) scale(0);
  transition: -webkit-transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
  -webkit-transform-origin: top;
          transform-origin: top;
  background: #616161e6;
  color: white;
  border-radius: 2px;
  font-size: 12px;
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

[data-md-tooltip]:hover:before {
  -webkit-transform: translateX(-50%) scale(1);
          transform: translateX(-50%) scale(1);
}

[data-md-tooltip].md-tooltip--right:before {
  left: auto;
  right: -10px;
  bottom: auto;
  top: 50%;
  -webkit-transform: translate(100%, -50%) scale(0);
          transform: translate(100%, -50%) scale(0);
  -webkit-transform-origin: left center;
          transform-origin: left center;
}

[data-md-tooltip].md-tooltip--right:hover:before {
  -webkit-transform: translate(100%, -50%) scale(1);
          transform: translate(100%, -50%) scale(1);
}

[data-md-tooltip].md-tooltip--left:before {
  left: -15px;
  right: auto;
  bottom: auto;
  top: 50%;
  -webkit-transform: translate(-100%, -50%) scale(0);
          transform: translate(-100%, -50%) scale(0);
  -webkit-transform-origin: right center;
          transform-origin: right center;
}

[data-md-tooltip].md-tooltip--left:hover:before {
  -webkit-transform: translate(-100%, -50%) scale(1);
          transform: translate(-100%, -50%) scale(1);
}

.md-tooltip--top:before {
