.slider-td .switch {
  position: relative;
  display: inline-block;
  width: 50px;
    height: 22px;
    margin-bottom: 0px;
}

.slider-td .switch input { 
  opacity: 0;
  width: 0;
  height: 0;
}

.slider-td .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: .4s;
}

.slider-td .slider:before {
  position: absolute;
    content: "";
    height: 16px;
    width: 16px;
    left: 4px;
    /* bottom: 4px; */
    top: 3px;
    background-color: white;
    transition: .4s;
}

.slider-td input:checked + .slider {
  background-color: #c4d2f4;
}

.slider-td input:focus + .slider {
  box-shadow: 0 0 1px #c4d2f4;
}

.slider-td input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider-td .slider.round {
  border-radius: 34px;
}

.slider-td .slider.round:before {
  border-radius: 50%;
}
