.arrow-link {
  position: relative;
  /* display: block; */
  /* padding: 1em 3em 1em 1em;
    font-size: 30px; */
  /* font-weight: bold; */
  /* text-decoration: none; */
  /* color: #e74c3c;
    box-shadow: inset 0 0 0 0.2em #e74c3c; */
  background-color: rgba(226, 102, 64, 0.63);
  color: #ffffff;
  padding: 2px 20px 2px 10px;
  font-size: 8px;
  line-height: 12px;
  display: inline-block;
}
.arrow-link:after,
.arrow-link:before {
  content: "";
  display: block;
  position: absolute;
  transition: all 0.5s ease-in-out;
}
.arrow-link:before {
  top: 50%;
  right: 1em;
  width: 1em;
  height: 1em;
  transform-origin: 50% 50%;
  transform: translate(0, -0.5em) rotate(5deg);
  /* box-shadow: inset -0.2em 0.2em 0 0 #e74c3c; */
  /* border-radius: 0 0.15em 0 0;  */
  box-shadow: inset -0.2em 0.2em 0 0 #ffffff;
}
.arrow-link:after {
  top: 50%;
  right: 1em;
  width: 1.2em;
  height: 0.2em;
  transform: translate(0, -0.1em) rotate(-40deg);
  /* background-color: #e74c3c; */
  background-color: #ffffff;
}
