/* Fade effect for ordered list CSS */
.card-body ol {
  position: relative;
}
.card-body ol:after {
  content: "";
  position: absolute;
  z-index: 1;
  bottom: 0;
  left: 0;
  pointer-events: none;
  background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0), rgba(255, 255, 255, 1) 90%);
  width: 100%;
  height: 4em;
}
/* Fade effect for ordered list CSS */

/* Global unordered list CSS */
.unordered-list ul {
  list-style: none;
  padding-left: 20px;
}

.unordered-list ul li::before {
  content: "\2022";
  color: #25429c;
  font-weight: bold;
  display: inline-block;
  width: 1em;
  margin-left: -1em;
}
.unordered-list ul li .list-imp {
  font-weight: 600;
  position: relative;
}
.unordered-list ul li .list-imp::before {
  position: absolute;
  content: "";
  background-image: url(./images/underline.svg);
  background-repeat: no-repeat;
  background-position: 0px 0px;
  width: 130px;
  height: 15px;
  top: 25px;
  left: 0%;
}
/* Global unordered list CSS */

/* Global ordered list CSS */
.ordered-list ol {
  list-style: none;
  counter-reset: item;
  margin-bottom: 0px;
}
.ordered-list li {
  counter-increment: item;
  margin-bottom: 20px;
}
.ordered-list li:before {
  margin-right: 10px;
  content: counter(item);
  background: #f8d874;
  border-radius: 100%;
  color: #25429c;
  width: 1.7em !important;
  text-align: center;
  display: inline-block;
  margin-left: -35px !important;
  font-weight: 700;
}
/* Global ordered list CSS */

